import * as React from "react";

const CanvasView = ({ data }: any) => {
  const canvasRef = React.useRef<HTMLCanvasElement>(null);
  const containerRef = React.useRef<HTMLDivElement>(null);

  let ctx = null;

  let cameraOffset: any = null;
  let cameraZoom = 1;
  let MAX_ZOOM = 5;
  let MIN_ZOOM = 0.1;
  let SCROLL_SENSITIVITY = 0.0005;
  let isDragging = false;
  let dragStart = { x: 0, y: 0 };

  React.useEffect(() => {
    if (canvasRef.current != null && containerRef.current != null) {
      ctx = canvasRef.current.getContext("2d");

      canvasRef.current.width = containerRef.current.clientWidth;
      canvasRef.current.height = containerRef.current.clientHeight;

      cameraOffset = {
        x: canvasRef.current.width / 2,
        y: canvasRef.current.height / 2,
      };
      // console.log("cameraOffset", cameraOffset);
      ctx.fillStyle = "red";
      ctx.fillRect(0, 0, 100, 100);

      setTimeout(() => {
        ctx.fillStyle = "blue";
        ctx.fillRect(0, 0, 50, 50);
        // zoom out by 100%
        ctx.scale(0.5, 0.5);
      }, 2000);
    }
  }, []);

  return (
    <div style={{ minHeight: "400px" }} ref={containerRef} className="w-full  ">
      <canvas
        ref={canvasRef}
        className="bordper w-full editor"
        id="canvas"
      ></canvas>
    </div>
  );
};

export default CanvasView;
